import React, { useState } from "react";
import { toast } from "react-toastify";

const AddAgent = ({ title, agent, sales, type, btnText, action }) => {
  const [agentName, setAgentName] = useState("");
  const [sale, setSales] = useState("");
  const [select, setSelect] = useState("daily");
  const [country, setCountry] = useState("Germany");
  const dailyAgentData = getLocalData("dailyAgentData") || [];
  const annulAgentData = getLocalData("annualAgentData") || [];
  const dailyCommandData = getLocalData("dailyCommandData") || [];
  const annualCommnadData = getLocalData("annualCommandData") || [];
  //  date store

  // agent
  const AddDailyAgent = async (AgentName, Agentsales) => {
    dailyAgentData?.push({ name: AgentName, sales: Agentsales });
    removeData("dailyAgentData");
    setLocalData("dailyAgentData", dailyAgentData);
  };
  const AddAnnualAgent = async (agentName, agentSales) => {
    annulAgentData?.push({ name: agentName, sales: agentSales });
    removeData("annualAgentData");
    setLocalData("annualAgentData", annulAgentData);
  };
  const updateDailyAgent = async (nameToUpdate, newSales) => {
    if (!nameToUpdate || !newSales) {
      toast.error("Required fields");
      return;
    }
    dailyAgentData.forEach((agent) => {
      if (agent?.name === nameToUpdate) {
      }
    });
    dailyAgentData.forEach((agent, index) => {
      if (agent.name === nameToUpdate) {
        dailyAgentData[index].sales =
          newSales !== undefined ? newSales : agent.sales;
      }
    });
    removeData("dailyAgentData");
    setLocalData("dailyAgentData", dailyAgentData);
  };
  const updateAnnualAgent = async (nameToUpdate, newSales) => {
    if (!nameToUpdate || !newSales) {
      toast.error("Required fields");
      return;
    }
    annulAgentData.forEach((agent, index) => {
      if (agent.name === nameToUpdate) {
        annulAgentData[index].sales =
          newSales !== undefined ? newSales : agent.sales;
      }
    });
    removeData("annualAgentData");
    setLocalData("annualAgentData", annulAgentData);
  };
  //  command
  const AddDailyCommand = async (name, sale) => {
    console.log(name, sale);
    dailyCommandData.push({ country: name, sales: sale });
    removeData("dailyCommandData");
    setLocalData("dailyCommandData", dailyCommandData);
  };
  const AddAnnualCommnad = async (name, sale) => {
    annualCommnadData.push({ country: name, sales: sale });
    removeData("annualCommandData");
    setLocalData("annualCommandData", annualCommnadData);
  };
  const updateDailyCommand = async (country, sales) => {
    dailyCommandData.forEach((ele, index) => {
      if (ele.country === country) {
        dailyCommandData[index].sales = sales !== undefined ? sales : ele.sales;
      }
    });
    removeData("dailyCommandData");
    setLocalData("dailyCommandData", dailyCommandData);
  };
  const updateAnnualCommand = async (country, newSales) => {
    annualCommnadData.forEach((ele, index) => {
      if (ele.country === country) {
        annualCommnadData[index].sales =
          newSales !== undefined ? newSales : ele.sales;
      }
    });
    removeData("annualCommandData");
    setLocalData("annualCommandData", annualCommnadData);
  };

  // handle
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (select === "daily" && type === "agent") {
        if (!agentName || !sale) {
          toast.error("Feild is require");
          return;
        }
        AddDailyAgent(agentName, sale);
        toast.success("Daily Agent Data Added");
      }
      if (select === "annually" && type === "agent") {
        if (!agentName || !sale) {
          toast.error("Feild is require");
          return;
        }
        AddAnnualAgent(agentName, sale);
        toast.success("Annually Agent Data Added");
      }
      if (select === "daily" && type === "command") {
        if (!country || !sale) {
          toast.error("Feild is require");
          return;
        }
        AddDailyCommand(country, sale);
        toast.success("Daily Command Data Added");
      }
      if (select === "annually" && type === "command") {
        if (!country || !sale) {
          console.log("som");
          toast.error("Feild is require");
          return;
        }
        AddAnnualCommnad(country, sale);
        toast.success("Annually Command Data Added");
      }
    } catch (error) {
      console.log(error);
    }
  };
  const handleComand = async (e) => {
    e.preventDefault();
    try {
      if (select === "daily" && type === "agent") {
        if (!agentName || !sale) {
          toast.error("Feild is require");
          return;
        }
        updateDailyAgent(agentName, sale);
        toast.success("Daily Agent Data updated");
      }
      if (select === "annually" && type === "agent") {
        if (!agentName || !sale) {
          toast.error("Feild is require");
          return;
        }
        updateAnnualAgent(agentName, sale);
        toast.success("Annually Agent Data Added");
      }
      if (select === "daily" && type === "command") {
        if (!country || !sale) {
          toast.error("Feild is require");
          return;
        }
        updateDailyCommand(country, sale);
        toast.success("Daily Command Data updated");
      }
      if (select === "annually" && type === "command") {
        if (!country || !sale) {
          toast.error("Feild is require");
          return;
        }
        updateAnnualCommand(country, sale);
        toast.success("Annually Command Data updated");
      }
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <>
      <div className="border p-3 bg-light rounded box-shadow">
        <form
          action=""
          onSubmit={action === "add" ? handleSubmit : handleComand}
        >
          <h5 className="text-center fw-bold">{title}</h5>
          <div className="m-4 mt-2 d-flex gap-3">
            {type !== "command" && (
              <input
                onChange={(e) => setAgentName(e.target.value)}
                type="text"
                className=" w-100 p-2 rounded"
                style={{ outline: "none" }}
                placeholder={agent}
              />
            )}
            {type === "command" && (
              <select
                onChange={(e) => setCountry(e.target.value)}
                name="commnadCountry"
                id=""
                className=" w-100 p-2 rounded"
                style={{ outline: "none" }}
              >
                <option value="Daily">Germany</option>
                <option value="Czech">Czech</option>
                <option value="France">France</option>
                <option value="France">France</option>
                <option value="Spain">Spain</option>
                <option value="Croatia">Croatia</option>
                <option value="Russia">Russia</option>
                <option value="Italy">Italy</option>
              </select>
            )}
          </div>
          <div className="m-4 d-flex gap-3">
            <input
              type="text"
              className=" w-100 p-2 rounded"
              style={{ outline: "none" }}
              placeholder={sales}
              onChange={(e) => setSales(e.target.value)}
            />
          </div>
          {(type === "agent" || type === "command") && (
            <div className="m-4 d-flex gap-3">
              <select
                onChange={(e) => setSelect(e.target.value)}
                name="dialyAnually"
                id=""
                className=" w-100 p-2 rounded"
                style={{ outline: "none" }}
              >
                <option value="daily">Daily</option>
                <option value="annually">Annually</option>
              </select>
            </div>
          )}
          {(type === "agentEdit" || type === "commandEdit") && (
            <div className="m-4 d-flex gap-3">
              <select
                onChange={(e) => setSelect(e.target.value)}
                name="dialyAnually"
                id=""
                className=" w-100 p-2 rounded"
                style={{ outline: "none" }}
              >
                <option value="daily">Daily</option>
                <option value="annually">Annually</option>
              </select>
            </div>
          )}
          <div className="mx-3">
            <button className="btn btn-green w-100">{btnText}</button>
          </div>
        </form>
      </div>
    </>
  );
};

export default AddAgent;

export const getLocalData = (key) => {
  return JSON.parse(localStorage.getItem(key));
};

export const setLocalData = (key, value) => {
  localStorage.setItem(key, JSON.stringify(value));
  return;
};

const removeData = (key) => {
  localStorage.removeItem(key);
  return;
};
