import React from "react";
import ApexCharts from "react-apexcharts";
const Graph = ({ yAxis, xAxis, color, title, labelColor, labelValue }) => {
  const series = [
    {
      data: yAxis,
    },
  ];

  const option = {
    chart: {
      type: "bar",
      height: 350,
    },

    plotOptions: {
      bar: {
        // borderRadius: 4,
        // horizontal: true,
      },
    },

    fill: {
      type: "solid",
      colors: [color],
    },

    dataLabels: {
      enabled: false,
    },
    xaxis: {
      categories: xAxis,
    },
  };

  return (
    <>
      <div className="m-3 p-2 py-3 bg-white rounded box-shadow">
        <h4 className="text-center fw-bold ">{title}</h4>
        <div className="d-flex justify-content-center my-2">
          <div
            className="label my-auto m-2"
            style={{ height: "10px", width: "40px", background: labelColor }}
          ></div>{" "}
          <span className="label-text">{labelValue}</span>
        </div>
        <div id="chart">
          <ApexCharts
            options={option}
            series={series}
            type="bar"
            height={350}
          />
        </div>
      </div>
    </>
  );
};

export default Graph;
