import React from "react";
import Graph from "../components/Graph";
import { useNavigate } from "react-router-dom";

const Home = () => {
  const navigate = useNavigate();
  const dailyAgentData = getLocalData("dailyAgentData");
  const dailyCommandData = getLocalData("dailyCommandData");
  const annualCommandData = getLocalData("annualCommandData");
  const annualAgentData = getLocalData("annualAgentData");

  const dailyAgent = getagent(dailyAgentData);
  const annulAgent = getagent(annualAgentData);
  const dailyCommand = getCommand(dailyCommandData);
  const annulCommand = getCommand(annualCommandData);
  console.log(annulCommand);
  return (
    <div className="container">
      <div className="row row-cols-md-2">
        <div className="p-4 ">
          <div className="bg-white p-5 m-5 text-center rounded">
            <h3 className="fw-bold">Total Amount</h3>
            <h3 className="text-primary">
              {JSON.parse(localStorage.getItem("totalAmount"))}
            </h3>
          </div>
        </div>

        <div className="p-4 ">
          <div className="bg-white p-5 m-5 text-center rounded ">
            <h3 className="fw-bold">Daily Target</h3>
            <h3 className="text-primary">
              {JSON.parse(localStorage.getItem("dailyTarget"))}
            </h3>
          </div>
        </div>
      </div>

      <div className="row row-cols-md-2 w-100 ">
        <div className="p-2">
          <Graph
            labelValue="Agent daily value"
            labelColor="var(--color-dark-cyan)"
            title="Daily Sales Agents"
            yAxis={dailyAgent?.ydata}
            xAxis={dailyAgent?.xdata}
            color={"var(--color-dark-cyan)"}
          />
        </div>
        <div className="p-2">
          <Graph
            labelValue="Teams daily value"
            labelColor="var(--color-transperent-dark-blue)"
            title="Daily Team Sales"
            yAxis={dailyCommand?.ydata}
            xAxis={dailyCommand?.xdata}
            color={"var(--color-transperent-dark-blue)"}
          />
        </div>
      </div>
      <div className="row row-cols-md-2 w-100">
        <div className="p-2">
          <Graph
            labelValue="Agent annully value"
            labelColor="var(--color-light-yellow)"
            title="Agents annual sales"
            yAxis={annulAgent?.ydata}
            xAxis={annulAgent?.xdata}
            color={"var(--color-light-yellow)"}
          />
        </div>
        <div className="p-2">
          <Graph
            labelValue="Command Anually value"
            labelColor="var(--color-light-green)"
            title="Annully Team Sales"
            yAxis={annulCommand?.ydata || []}
            xAxis={annulCommand?.xdata || []}
            color={"var(--color-light-green)"}
          />
        </div>
      </div>
      <div className="text-center mt-4 pb-4">
        <button
          onClick={() =>
            navigate("/dashboard-graph/editor", { state: "Editor Page" })
          }
          className="btn btn-green"
        >
          Editor Page
        </button>
      </div>
    </div>
  );
};

export default Home;

const getLocalData = (key) => {
  return JSON.parse(localStorage.getItem(key));
};

const getagent = (data) => {
  const ydata = [];
  const xdata = [];
  if (Array.isArray(data)) {
    data.forEach((d) => {
      ydata.push(d.sales);
      xdata.push(`${d.name} (sales:${d.sales})`);
    });
  }
  return { ydata, xdata };
};

const getCommand = (data) => {
  const ydata = [];
  const xdata = [];
  if (Array.isArray(data)) {
    data.forEach((d) => {
      ydata.push(d.sales);
      xdata.push(`${d.country} (sales:${d.sales})`);
    });
  }
  return { ydata, xdata };
};
