import React, { useState } from "react";
import "./Editor.css";
import AddAgent from "./EditerInterFace";
import { toast } from "react-toastify";
const EditorCom = () => {
  const [dailyTarget, setDailyTarget] = useState("");
  const [totalAmount, settotalAmount] = useState("");
  const handleDaily = async (e) => {
    e.preventDefault();
    try {
      if (!dailyTarget) {
        toast.error("Enter daily target");
        return;
      }
      localStorage.setItem("dailyTarget", JSON.stringify(dailyTarget));
      toast.success("Successfully added daily target");
    } catch (error) {
      console.log(error);
    }
  };
  const handleAmount = async (e) => {
    e.preventDefault();
    try {
      if (!totalAmount) {
        toast.error("Enter Total Amount");
        return;
      }
      localStorage.setItem("totalAmount", JSON.stringify(totalAmount));
      toast.success("Successfully added total amount");
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <div className="row row-cols-md-2 w-100">
        <div className="p-2">
          <form action="" onSubmit={handleDaily}>
            <div className="m-4 d-flex gap-3">
              <input
                onChange={(e) => setDailyTarget(e.target.value)}
                type="text"
                className=" w-100 p-2 rounded"
                style={{ outline: "none" }}
                placeholder="Enter Total"
              />
              <button className="btn btn-blue w-50">Change Daily Target</button>
            </div>
          </form>
        </div>
        <div className="p-2">
          <form action="" onSubmit={handleAmount}>
            <div className="m-4 d-flex gap-3">
              <input
                onChange={(e) => settotalAmount(e.target.value)}
                type="text"
                className=" w-100 p-2 rounded"
                style={{ outline: "none" }}
                placeholder="Enter Total"
              />
              <button className="btn btn-red w-50">Change Total Amount</button>
            </div>
          </form>
        </div>
      </div>
      <div className="row row-cols-md-2 w-100">
        <div className="p-4">
          <AddAgent
            action="add"
            title="Add agent details"
            agent="Agent name"
            type="agent"
            sales="Sales"
            btnText="Add agent details "
          />
        </div>
        <div className="p-4">
          <AddAgent
            action="add"
            title="Add command data "
            agent="Agent name"
            type="command"
            sales="Sales"
            btnText="Add command details "
          />
        </div>
        <div className="p-4">
          <AddAgent
            action="update"
            title="Agent data editor"
            type="agent"
            agent="Agent name"
            sales="Sales"
            btnText="Add agent"
          />
        </div>
        <div className="p-4">
          <AddAgent
            action="update"
            agent="Agent name"
            title="Agent annual data editor"
            type="command"
            sales="Sales"
            btnText="Add agent Details (Annually)"
          />
        </div>
      </div>
    </>
  );
};

export default EditorCom;
